<template>
  <div class="jdy-content jdy-resources fleft">
    <div class="jdy-content-inner-trip">
      <div class="resources-tab">
        <div>
          <div class="inlineblock mr10 ">
            :
            <el-select v-model="search.dType" clearable placeholder="" class="jdy-search-edit hasbg mr10">
              <el-option v-for="type in originType" :key="type.code" :label="type.name" :value="type.code">
              </el-option>
            </el-select>
          </div>
          <div class="inlineblock mr10 ">
            :
            <el-input placeholder="" v-model="search.dName" class="w200">
            </el-input>
          </div>
          <el-button type="primary" @click="getTableData" class="el-button btnbg el-button--primary btnInTab"></el-button>
        </div>
        <div>
          <el-button type="default" class="btnInTab" @click="refresh" v-loading.fullscreen.lock="fullscreenLoading"></el-button>
          <el-button type="primary" class="btnInTab" @click="add"></el-button>
        </div>
      </div><!--jdy-search end-->


      <div class="jdy-table jdy-table-padding">
        <el-table :data="tableData" border class=" all">
          <el-table-column label="" type="index" width="60">
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ scope.row.dName }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              <span v-if="scope.row.dType==1"></span>
              <span v-if="scope.row.dType==2"></span>
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ scope.row.dProvince }}-{{ scope.row.dCity }}-{{ scope.row.dArea }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ scope.row.goCount }}&nbsp;&nbsp;{{ scope.row.backCount }}
            </template>
          </el-table-column>
          <el-table-column label="" width="250" fixed="right">
            <template scope="scope">
              <el-button type="default" size="mini" @click="edit(scope.row)" class="mr15">
                
              </el-button>
              <el-button type="default" class="red mr15" @click="carManage(scope.row.id,scope.row.dName,scope.row.dCity)" size="mini">
                
              </el-button>
              <el-button type="default" class="red" @click="del(scope.row.id)" size="mini">
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--    begin-->
        <div class="clearfix">
          <el-pagination class="fright mt20" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                         :current-page.sync="currentPage" :page-size="pageSize" layout="prev, pager, next, jumper"
                         :total="tableDataTotal">
          </el-pagination>
        </div>
        <!--    end-->

      </div>
      <!--jdy-table end-->

      <!--jdy-alert begin -->
      <jdy-alert title="/" @closeAlert="departurePlaceClose" v-if="departurePlaceFlag"
                 class="alertDeparturePlace alertTag dialog-lv" style="width:500px;">

        <el-form :model="departurePlaceForm" :rules="departurePlaceRule" ref="departurePlaceForm" label-width="90px" class="p10 pt20">
          <el-form-item label=":" prop="dName">
            <el-input v-model="departurePlaceForm.dName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label=":" prop="dName">
            <el-select v-model="departurePlaceForm.dType" placeholder="">
              <el-option v-for="type in originTypeAdd" :key="type.code" :label="type.name" :value="type.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="dProvince">
            <el-select v-model="departurePlaceForm.dProvince" placeholder="" @change="provinceChange">
              <el-option :key="province.id" :label="province.name" :value="province.name" v-for="province in provinceList">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="dCity">
            <el-select v-model="departurePlaceForm.dCity" placeholder=""  @change="cityChange">
              <el-option v-for="item in cityList" :value="item.name" :label="item.name" :key="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="/:" prop="dArea">
            <el-select v-model="departurePlaceForm.dArea" placeholder="/">
              <el-option v-for="item in AreaList" :value="item.name" :key="item.name" :label="item.name">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="alertfoot1 clearfix">
          <el-button class="fright mt10 mr10" @click="departurePlaceClose"></el-button>
          <el-button type="primary" class="fright mt10 mr10"  :disabled="btnFlag" @click="submitForm('departurePlaceForm')"></el-button>
        </div>
      </jdy-alert>

      <!--jdy-alert end-->

    </div><!--jdy-content-inner-trip end-->
  </div><!--jdy-content end-->
</template>

<script>
  import jdyAlert from '@/components/Alert';
  import oData from './o.js';
  export default {
    name: 'Schedule',
    data() {
      return {
        btnFlag:false,
        search: {
          dType: 3,
          companyId: JSON.parse(sessionStorage.loginData).uCompanyId,
          dName: null,
          currPage: 0,
          pageSize: 20,
          dStatus: 0
        },
        originType: [{code: 3, name: ''}, {code: 1, name: ''}, {code: 2, name: ''}],
        originTypeAdd: [ {code: 1, name: ''}, {code: 2, name: ''}],
        tableData: [],
        currentPage: 1, //,
        pageSize: 20,
        tableDataTotal: 0,
        departurePlaceFlag: false,
        departurePlaceForm: {
          id:null,
          dName:null,
          dType:null,
          dProvince:null,
          dCity:null,
          dArea:null,
          companyId:JSON.parse(sessionStorage.loginData).uCompanyId
        },
        departurePlaceRule:{
          dName:[{required: true,
            message: '',
            trigger: 'blur'}],
          dType:[{required: true,
            message: '',
            trigger: 'change'}],
          dProvince:[{required: true,
            message: '',
            trigger: 'change'}],
          dCity:[{required: true,
            message: '',
            trigger: 'change'}],
          dArea:[{required: true,
            message: '',
            trigger: 'change'}]
        },
        loading: false,
        selectList: [],
        provinceList:[],
        cityList:[],
        AreaList:[],
        fullscreenLoading:false,
        companyType: null
      }
    },
    methods: {
        //
      searchCompany(value){
        this.loading = true;
        let url = api_prefix + '/Company/selectAllTopCompany';
        let data = {pageIndex: 1};
        if (value) {
          data.name = value;
        }
        this.$http.post(url, data, {emulateJSON: true}).then(response => {
          if (response.body.code === "-1") {
            this.$alert(response.body.message, "", {type: 'warning'});
            return
          }
          this.selectList = response.body.body.list
          this.loading = false;
        }, response => {
          this.$alert("~", "", {type: 'warning'});
          this.loading = false;
        });
      },
      //
      getTableData() {
        // if (!this.search.companyId) {
        //   this.$alert("~", "", {type: "warning"});
        //   return;
        // }
        this.$http.post(api_prefix + 'departure/list', this.search).then(response => {
          if (response.body.body.code = "0") {
            var data = response.data.body;
            this.tableData = data.resultList;
            this.tableDataTotal = data.totalNum;
          } else {
            this.$alert(response.body.body.message, "", {type: "error"});
          }
        }, response => {
          this.$alert("", "", {type: "error"});
        });
      },
      ///
      add() {
        var alertClass = 'alertDeparturePlace';
        this.departurePlaceFlag = true;

        if (this.departurePlaceFlag) {
          this.$nextTick(function () {
            jdyFn.setAlertStyle(alertClass);
            // this.search.addProduct = '';
          });
        }
      },
      //
      edit(form) {
        this.departurePlaceForm = JSON.parse(JSON.stringify(form));
        this.provinceChange(form.dProvince,true,()=>{
          this.cityChange(form.dCity,true);
        });
        this.add();
      },
      //
      del(id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post(api_prefix + 'departure/del', {"companyId": this.search.companyId, "id": id, "userId": 0}).then(response => {
            console.log(response)
            if(response.body.code == '0'){
              this.$message.success("");
              this.getTableData();
            }else{
              this.$message.error("");
            }
          }, response => {
            this.$message.error("");
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      carManage(id,name,city) {
        this.$router.push({name: 'origincar', query: {id: id,name:name,city:city}});
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      //
      handleCurrentChange(val) {
        if (val) {
          this.search.currPage = val;
          this.getTableData();
          this.currentPage = val;
        }
      },
      ///
      departurePlaceClose() {
        this.btnFlag = false;
        this.departurePlaceFlag = false;
        $('.alertbgg').remove();
        this.departurePlaceForm = {
          id:null,
          dName:null,
          dType:null,
          dProvince:null,
          dCity:null,
          dArea:null,
          companyId:JSON.parse(sessionStorage.loginData).uCompanyId
        }
      },
      ///
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
              let url = null;
              if(this.departurePlaceForm.id){
                  url = 'departure/update'
              }else{
                  url = 'departure/save'
              }
            this.btnFlag = true;
            this.$http.post(api_prefix + url,this.departurePlaceForm
            ).then(response => {
              this.btnFlag = false;
              console.log(response);
              if(response.body.code == "0"){
                  this.$message.success("");
                  this.getTableData();
                  this.departurePlaceClose();
              }else{
                  this.$alert(response.body.message,"",{type:"warning"});
              }
            }, response => {
                this.btnFlag = false;
                this.$alert("","",{type:"warning"});
            });
          } else {
              this.$message.error("");
              return false;
          }
        });
      },
      //
      defaultProvinceData() {
        let url = api_prefix + '/City/selectCities';
        let city = { level: 1 }
        this.$http.post(url, city).then(response => {
          if (response.body.code === "-1") {
            this.$alert(response.body.message, "", { type: 'error' });
            return
          }
          this.provinceList = response.body.body
        }, response => {
          this.$alert("~", "", { type: 'error' });
        });
      },
      //
      provinceChange(value,isEditFlag,fun) {
        let id = 0;
        for (var i in this.provinceList) {
          if (this.provinceList[i].name == value) {
            id = this.provinceList[i].id;
          }
        }
        if (id == 0) {
          return;
        }
        let url = api_prefix + '/City/selectCities';
        let city = { level: 2, pid: id }
        this.$http.post(url, city).then(response => {
          if (response.body.code === "-1") {
            this.$alert(response.body.message, "", { type: 'error' });
            return
          }
          this.cityList = response.body.body;
          if(!isEditFlag){
            this.departurePlaceForm.dCity = null
            this.departurePlaceForm.dArea = null
          }
          fun && fun()
        }, response => {
          this.$alert("~", "", { type: 'error' });
        });
      },
      // 
      cityChange(value,isEditFlag) {
        let id = 0;
        for (var i in this.cityList) {
          if (this.cityList[i].name == value) {
            id = this.cityList[i].id;
          }
        }
        if (id == 0) {
          return;
        }
        let url = api_prefix + '/City/selectCities';
        let city = { level: 3, pid: id }
        this.$http.post(url, city).then(response => {
          if (response.body.code === "-1") {
            this.$alert(response.body.message, "", { type: 'error' });
            return
          }
          this.AreaList = response.body.body
          if(!isEditFlag){
            this.departurePlaceForm.dArea = null
          }
        }, response => {
          this.$alert("~", "", { type: 'warning' });
        });
      },
      refresh(){
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          });
          this.getTableData();
        }, 1000);
      }
    },
    created() {
      let logindata = JSON.parse(sessionStorage.loginData)
      this.companyType = logindata.cType
      this.search.companyId = logindata.uCompanyId
      console.log(this.search.companyId,'this.search.companyId')
      this.defaultProvinceData();
      this.searchCompany();
      this.getTableData();
    },
    components: {
      jdyAlert
    }
  }
</script>
<style scoped>
.alertDeparturePlace{
  height: 420px;
}

</style>



