import Vue from 'vue'


//
Vue.filter('moneyTwoPoints',function (val,type) {
  var returnVal = '0.00';
  if(val == null|| typeof val == 'undefined'){
    returnVal = '';
    return returnVal;
  }
  else{
    var selVal = $.trim(val.toString());
    if(selVal == ''){
      returnVal = '';
      return returnVal;
    }
    else{
      if(selVal.indexOf('.') == -1){   //
        returnVal = selVal+".00";
      }
      else{
        var arrNum = selVal.split(".");
        var pointNum = arrNum[1];
        if(pointNum.length == 1){ //
          returnVal = selVal +'0';
        }
        else if(pointNum.length == 2){ //
          returnVal = selVal;
        }
        else{     //
          arrNum[1] = pointNum.substring(0,2);
          returnVal = arrNum.join('.');
        }
      }
    }
  }
  if(type==1){
    return returnVal;
  }else{
    return ''+returnVal;
  }
  
});

