  //
Vue.filter('addDate',function (value,days) {
  if(value== '' || value == null || typeof value == 'undefined' || isNaN(value)){
    return '';
  }
  var a = new Date(value);
  a = a.valueOf();
  a = a + (days-1) * 24 * 60 * 60 * 1000;
  a = new Date(a);
  return a;
});

