<template>
  <div class="jdy-content jdy-resources fleft noborder">
    <div class="jdy-content-inner-trip">
      <div class="jdy-search clearfix p10">
        <div class="inlineblock mr10 ">
          :
          <el-select v-model="search.sbReturn" clearable placeholder="" class="jdy-search-edit  mr10">
            <el-option v-for="type in types" :key="type.code" :label="type.name" :value="type.code">
            </el-option>
          </el-select>
        </div>
        <div class="inlineblock mr10 ">
          :
          <el-input placeholder="" v-model="search.sbStartPoint" class="w200">
            <el-button slot="append" icon="search" class="btnbg" @click="getTableData"></el-button>
          </el-input>
        </div>
        <el-button type="primary" class="fright mt10" @click="add"></el-button>
        <el-button type="default" class="fright mt10 mr10" @click="goback('origin')"></el-button>
      </div>
      <!--jdy-search end-->

      <div class="jdy-table p10">
        <p v-if="showFlag!=1"></p>
        <el-table :data="goTableData" border class=" all mt10" v-if="showFlag!=1">
          <el-table-column label="" width="">
            <template scope="scope">
              {{formatDate(new Date(scope.row.sbTime), "hh:mm")}}
            </template>
          </el-table-column>
          <el-table-column label="()" width="">
            <template scope="scope">
              {{scope.row.sbTimeLength}}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ $route.query.name }}&nbsp;&nbsp;{{ scope.row.sbStartPoint }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ parseFloat(scope.row.sbPrice).toFixed(2)}}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ getEffctiveDay(scope.row.sbEffectWeek) || "" }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{formatDate(new Date(scope.row.sbStartTime), "yyyy-MM-dd")}}
              &nbsp;&nbsp; {{formatDate(new Date(scope.row.sbEndTime), "yyyy-MM-dd")}}
            </template>
          </el-table-column>
          <el-table-column label="" width="250" fixed="right">
            <template scope="scope">
              <el-button type="default" size="mini" @click="edit(scope.row)">
                
              </el-button>
              <el-button type="default" class="red" @click="del(scope.row.id)" size="mini">
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>


        <p class="mt30" v-if="showFlag!=0"></p>
        <el-table :data="returnTableData" border class=" all mt10" v-if="showFlag!=0">
          <!-- <el-table-column label="" type="index" width="60">
          </el-table-column> -->
          <el-table-column label="" width="">
            <template scope="scope">
              <!--{{formatDate(new Date(scope.row.sbTime), "hh:mm")}}-->
              
            </template>
          </el-table-column>
          <el-table-column label="()" width="">
            <template scope="scope">
              {{scope.row.sbTimeLength}}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ scope.row.sbStartPoint }}&nbsp; &nbsp;{{$route.query.name }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ parseFloat(scope.row.sbPrice).toFixed(2) }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{ getEffctiveDay(scope.row.sbEffectWeek) || "" }}
            </template>
          </el-table-column>
          <el-table-column label="" width="">
            <template scope="scope">
              {{formatDate(new Date(scope.row.sbStartTime), "yyyy-MM-dd")}}
              &nbsp;&nbsp;{{formatDate(new Date(scope.row.sbEndTime), "yyyy-MM-dd")}}
            </template>
          </el-table-column>
          <el-table-column label="" width="250" fixed="right">
            <template scope="scope">
              <el-button type="default" size="mini" @click="edit(scope.row)">
                
              </el-button>
              <el-button type="default" class="red" @click="del(scope.row.id)" size="mini">
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>


        <!--    begin-->
        <div class="clearfix">
          <el-pagination class="fright mt20" @current-change="handleCurrentChange"
                         :current-page.sync="search.currPage" :page-size="search.pageSize"
                         layout="prev, pager, next, jumper"
                         :total="search.tableDataTotal">
          </el-pagination>
        </div>
        <!--    end-->

      </div>
      <!--jdy-table end-->

      <!--jdy-alert begin -->
      <jdy-alert title="/" @closeAlert="departurePlaceClose" v-if="departurePlaceFlag"
                 class=" alertDeparturePlace2 alertTag alertJournal dialog-lv" style="width:750px;">

        <el-form :model="departurePlaceForm" :rules="departurePlaceRule" ref="departurePlaceForm"
                 class="demo-form-inline" label-width="120px">
          <div class="clearfix p10 mt15 alertHeight">
            <div class="alertScroll pr50">
              <el-row>
                <el-form-item label=":" prop="sbReturn">
                  <el-select v-model.number="departurePlaceForm.sbReturn" placeholder="" class="all" @change="returnTypeChange">
                    <el-option v-for="type in shortTypes" :key="type.code" :label="type.name" :value='type.code'>
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label=":" prop="sbTraffic">
                  <el-select v-model.number="departurePlaceForm.sbTraffic" placeholder="" class="all">
                    <el-option v-for="type in dTypes" :key="type.code" :label="type.name" :value="type.code">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label=":" prop="dName">
                  <el-time-picker v-model="departurePlaceForm.sbTime" :disabled="departurePlaceForm.sbReturn===1" :picker-options="{
                                format: 'HH:mm'
                              }" format="HH:mm" placeholder="" clearable :editable="false" style="width: 100%;">
                  </el-time-picker>
                </el-form-item>
                <el-form-item label="(min):" prop="sbTimeLength">
                  <el-input-number style="width: 100%;" :min=0 :controls="false"
                                   v-model.number="departurePlaceForm.sbTimeLength"
                                   placeholder=""></el-input-number>
                </el-form-item>
                <el-form-item label=":" prop="sbPrice">
                  <el-input v-model="departurePlaceForm.sbPrice" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="():" prop="sbStartPoint">
                  <!--<el-input v-model="departurePlaceForm.sbStartPoint" placeholder=""></el-input>-->
                  <el-select  v-model="departurePlaceForm.sbStartPoint" placeholder="" class="all" @change="startPoinChange" :filterable="true" :remote-method="searchStart" clearable remote>
                    <el-option :value="item.dName" :label="item.dName" :key="item.id" v-for="item in startList"></el-option>
                  </el-select>
                </el-form-item>
                <el-row>
                  <el-col :span="12">
                      <el-form-item label=":" prop="sbStartTime">
                        <el-date-picker
                          v-model="departurePlaceForm.sbStartTime"
                          type="date"
                          placeholder=""
                          class="all"
                          :picker-options="pickerOptions0" style="width: 100%;">
                        </el-date-picker>
                      </el-form-item>
                  </el-col>
                  <el-col :span="12">
                    <el-form-item label=":" prop="sbEndTime">
                      <el-date-picker
                        v-model="departurePlaceForm.sbEndTime"
                        type="date"
                        placeholder=""
                        class="all"
                        :picker-options="pickerOptions0" style="width: 100%;">
                      </el-date-picker>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item label=":" prop="sbEffectWeek">
                  <el-checkbox label="all" @change="selectAllDay" style="float:left;margin-right:10px"></el-checkbox>
                  <el-checkbox-group v-model="departurePlaceForm.sbEffectWeek" class="all">
                    <el-checkbox :key="value" :label="index" v-for="(value,index) in WeekDay">{{value}}</el-checkbox>
                  </el-checkbox-group>
                </el-form-item>
              </el-row>
              <el-form-item label=":" prop="dName">
                <el-checkbox v-model="useInBack" :disabled="!(!departurePlaceForm.id && departurePlaceForm.sbReturn===0)"></el-checkbox>
              </el-form-item>
              <el-row>
                <el-form-item label=":" prop="dArea">
                  <div class="c5e">1. </div>
                  <div class="mt05 c5e">2. 10:30</div>
                  <div class="mt05 c5e">3. 30</div>
                </el-form-item>
              </el-row>
            </div>
          </div>
          <div class="alertfoot1 clearfix" style="height:100px">
            <el-button class="fright mt10 mr10" @click="departurePlaceClose"></el-button>
            <el-button type="primary" class="fright mt10 mr10"  :disabled="btnFlag" @click="submitForm('departurePlaceForm')"></el-button>
          </div>
        </el-form>
      </jdy-alert>

      <!--jdy-alert end-->

    </div>
    <!--jdy-content-inner-trip end-->
  </div>
  <!--jdy-content end-->
</template>

<script>
  import jdyAlert from '@/components/Alert';
  import oData from './o.js';
  import ElCheckbox from "../../../../node_modules/element-ui/packages/checkbox/src/checkbox";
  import ElRow from "element-ui/packages/row/src/row";
  import ElCol from "element-ui/packages/col/src/col";
  export default {
    name: 'Schedule',
    data() {
      return {
        btnFlag:false,
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        showFlag:0,
        types: [{code: 0, name: ''}, {code: 1, name: ''}, {code: 2, name: ''}],
        shortTypes: [{code: 0, name: ''}, {code: 1, name: ''}],
        search: {
          sbReturn: 2,
          sbStartPoint: null,
          sbDepartureId: this.$route.query.id,
          currPage: 1,
          pageSize: 10,
          tableDataTotal: 0,
          sbStatus:0
        },
        returnTableData: [],
        goTableData: [],
        departurePlaceFlag: false,
        departurePlaceForm: {
          sbDepartureId:this.$route.query.id,
          id: null,
          sbTime: null,//
          sbTraffic: '',//
          sbReturn: '', //
          sbTimeLength: null,//
          sbPrice: null,//
          sbShuttleStopId:null,
          sbStartPoint: null,//
          sbStartTime: null, //
          sbEndTime: null, //
          sbEffectWeek: [],
          useForBack:0
        },
        useInBack:false,
        departurePlaceRule: {
          sbTraffic: [{
            required: true,
            type: 'number',
            message: '',
            trigger: 'change'
          }],
          sbReturn: [{
            required: true,
            type: 'number',
            message: '',
            trigger: 'change'
          }],
          sbStartPoint: [{
            required: true,
            message: '',
            trigger: 'change'
          }],
          sbStartTime: [{
            required: true,
            type: 'date',
            message: '',
            trigger: 'change'
          }],
          sbEndTime: [{
            required: true,
            type: 'date',
            message: '',
            trigger: 'change'
          }],
          sbEffectWeek: [{
            required: true,
            type: 'array',
            message: '',
            trigger: 'change'
          }]
        },
        startList: [],
        radio: 1,
        startName:'',
        dTypes: [{code: 0, name: ''}, {code: 1, name: ''}, {code: 2, name: ''}, {code: 3, name: ''}],
        simpleWeekDay: ["", "", "", "", "", "", ""], //
        WeekDay: ["", "", "", "", "", "", ""], //
        weekDayValue:[0,1,2,3,4,5,6]
      }
    },
    methods: {
      startPoinChange(value){
          this.startList.forEach(start => {
              if(start.dName == value){
                  this.departurePlaceForm.sbShuttleStopId = start.id
              }
          });
      },
      returnTypeChange(value){
          if(value===1){
              this.departurePlaceForm.sbTime = null
              this.useInBack = false;
          }
      },
      goback(url){
        this.$router.push({name:"origin"})
    },
    getTableData() {
      let data = {};
      if (this.search.sbReturn == 2) {  //sbReturn
        Object.assign(data, this.search, {sbReturn: null})
      } else {
        data = this.search
      }
      this.$http.post(api_prefix + 'shuttle_bus/list', data).then(response => {
        if (response.body.code == '0') {
          this.showFlag = this.search.sbReturn;
          this.search.tableDataTotal = response.body.body.totalNum;
          if (this.search.sbReturn === 0) { //
            this.goTableData = response.body.body.resultList;
          }
          else if (this.search.sbReturn === 1) {//
            this.returnTableData = response.body.body.resultList;
          } else {  //
            this.goTableData = [];
            this.returnTableData = [];
            response.body.body.resultList.forEach(row => {
              if (row.sbReturn === 0) {
                this.goTableData.push(row)
              } else {
                this.returnTableData.push(row)
              }
            });
          }
        } else {
          this.$alert(response.body.message, '', {type: 'warning'});
        }
      }, response => {
        this.$alert('', '', {type: 'warning'});
      });
    },
    getEffctiveDay(value) {  //
      let str = "";
      value.toString().split("").forEach((value, index) => {
        if (!isNaN(value) && parseInt(value)) {
          str += this.simpleWeekDay[index]
        }
      })
      return str;
    },
    formatDate(date, fmt) {  //
      if (date instanceof Date) {
        var o = {
          "M+": date.getMonth() + 1, //
          "d+": date.getDate(), //
          "h+": date.getHours(), //
          "m+": date.getMinutes(), //
          "s+": date.getSeconds(), //
          "q+": Math.floor((date.getMonth() + 3) / 3), //
          "S": date.getMilliseconds() //
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
          if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
      }
      throw new Error("");
    },
    add() {
      var alertClass = 'alertDeparturePlace2';
      this.departurePlaceFlag = true;

      if (this.departurePlaceFlag) {
        this.$nextTick(function () {
          jdyFn.setAlertStyle(alertClass);
          // this.search.addProduct = '';
        });
      }
    },
    edit(form) {
      this.departurePlaceForm = this.reFormmartFormDate(form);
      console.log(this.departurePlaceForm);
      this.add();
    },
    reFormmartFormDate(form){
      let array = [];
      if(form.sbEffectWeek){
        form.sbEffectWeek.split('').forEach((day,index) => {if(day == 1){array.push(index)}});
      }
      return Object.assign({},form,{sbEffectWeek:array,sbStartTime:new Date(form.sbStartTime),sbEndTime:new Date(form.sbEndTime),sbTime:new Date(form.sbTime)});
    },
    del(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$http.post(api_prefix + 'shuttle_bus/del',{"id": id,sbDepartureId:this.$route.query.id}
        ).then(response => {
          if(response.body.code == '0'){
            this.$message.success("");
            this.getTableData();
          }else{
            this.$alert(response.body.message,'',{type:'warning'});
          }
        }, response => {
          this.$alert('','',{type:'warning'});
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    loadStartList(){
      this.$http.post(api_prefix + 'departure/start_site_group', {"dName":this.startName}).then((response) => {
        this.startList = response.body.body.resultList;
        console.log(this.startList);
      }, response => {
        this.$alert("", '', {type: 'warning'});
      });
    },
    searchStart(name){
      this.startName = name;
      this.loadStartList();
    },
    handleCurrentChange(val) {
      if (val) {
        this.search.currPage = val
        this.getTableData();
      }
    },
    departurePlaceClose() {
      this.btnFlag = false;
      this.departurePlaceFlag = false;
      $('.alertbgg').remove();
      this.useInBack = false;
      this.departurePlaceForm= {
        id:null,
        sbTime: null,//
        sbTraffic: '',//
        sbReturn: '', //
        sbTimeLength: null,//
        sbPrice: null,//
        sbStartPoint: null,//
        sbStartTime: null, //
        sbEndTime: null, //
        sbEffectWeek: [],
        sbDepartureId:this.$route.query.id,
        useForBack:0
      }
    },
    submitForm(refName){
      console.log(this.useInBack);
      this.$refs[refName].validate(valid => {
        if (valid) {
          let data = this.formatSubmitDate();
          let url = null;
          if (data.id) {
            url = api_prefix + 'shuttle_bus/update';
          } else {
            url = api_prefix + 'shuttle_bus/save';
          }
          this.btnFlag = true;
          this.$http.post(url, data).then(response => {
            this.btnFlag = false;
            if (response.body.code == '0') {
              this.$message.success("");
              this.departurePlaceClose();
              this.getTableData();
            } else {
              this.$alert(response.body.message, '', {type: 'warning'});
            }
          }, response => {
            this.btnFlag = false;
            this.$alert('', '', {type: 'warning'});
          });
        } else {
          this.$message.error("");
        }
      });
    },
    formatSubmitDate(){
      let data = {};
      let days = '0000000';
      days = days.split('').map((day, index) => {
        if (this.departurePlaceForm.sbEffectWeek.indexOf(parseInt(index)) != -1) {
          return 1
        } else {
          return 0;
        }
      }).join('');
      Object.assign(data, this.departurePlaceForm, {
        sbEffectWeek: days,
        sbStartTime: this.formatDate(this.departurePlaceForm.sbStartTime, 'yyyy-MM-dd'),
        sbEndTime: this.formatDate(this.departurePlaceForm.sbEndTime, 'yyyy-MM-dd')
      })
      if(!data.id && this.useInBack){
        data.useForBack = 1
      }
      return data;
    },
    selectAllDay(event){
      this.departurePlaceForm.sbEffectWeek = event.target.checked ? this.weekDayValue : [];
    }
  },
  created() {
    this.getTableData();
      this.loadStartList();
    },
    components: {
      ElCol,
      ElRow,
      ElCheckbox,
      jdyAlert
    }
  }
</script>
<style scoped>
  .jdyalert{height: auto;}
  .alertScroll{height: 81%;}
  .alertHeight{height: 81%;}
</style>



