<template>
    <div class="jdy-content jdy-resources fleft ">
        <div class="jdy-content-inner-trip">
            <div class="tripbox-tab clearfix">
                <a href="javascript:;" title="" class="relative inlineblock fleft active" @click="goSpotInfo($route.query.type)">
                    <span class="absolute inlineblock"></span>
                </a>
                <a title="" href="javascript:;" class="relative inlineblock  fleft" @click="goPic($route.query.type)" v-show="picShow">
                    <span class="absolute inlineblock"></span>
                </a>
            </div>
            <el-form :model="ruleform" :rules="rules" ref="ruleform" class="demo-form" label-width="150px">
                <el-row class="mt30  pr50">
                    <el-col :span="12">
                        <el-form-item label=":" prop="sName">
                            <el-input v-model="ruleform.sName" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="ruleform.sSortName" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="ruleform.sPhone" placeholder="">
                            </el-input>
                        </el-form-item>
                        <!---->
                        <el-form-item label=":" prop="sAttribute">
                            <el-select v-model="ruleform.sAttribute" clearable placeholder="" class="all">
                                <el-option v-for="item in attributeList" :value="item.id" :label="item.dName" :key="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input  v-model="ruleform.sAdditional" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input type="textarea" v-model="ruleform.sIntroduce" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <!--<jdy-editor :content = "ruleform.sDetail" attr="pcontent" @input="editorUpdate"></jdy-editor>-->
                            <el-input type="textarea" v-model="ruleform.sDetail" placeholder="">
                            </el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label=":" prop="sProvince">
                            <el-select v-model="ruleform.sProvince" clearable placeholder="" class="all" @change="provinceChange">
                                <el-option v-for="item in provinceList" :value="item.name" :label="item.name" :key="item.id">
                                    <span :kes="item.id">{{item.name}}</span>
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":" prop="sCity">
                            <el-select v-model="ruleform.sCity" clearable placeholder="" class="all" @change="cityChange">
                                <el-option v-for="item in cityList" :value="item.name" :label="item.name" :key="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="/:" prop="sArea">
                            <el-select v-model="ruleform.sArea" clearable placeholder="" class="all" @change="areaChange">
                                <el-option v-for="item in AreaList" :value="item.name" :key="item.name" :label="item.name">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input  v-model="ruleform.sAdress" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="sCustomPlace">
                            <el-input  v-model="ruleform.sCustomPlace" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-time-picker v-model="ruleform.sStartTime" placeholder=""></el-time-picker>
                            <el-time-picker v-model="ruleform.sEndTime" placeholder=""></el-time-picker>
                        </el-form-item>
                        <!---->
                        <el-form-item label=":" prop="sLevel">
                            <el-select v-model="ruleform.sLevel" clearable placeholder="" class="all">
                                <el-option v-for="item in levelList" :value="item.id" :label="item.dName" :key="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":" id="mapPosition">
                            <jyd-map :bDMapx='bDMapx' :bDMapy='bDMapy' :bcityName='bcityName' @mapClick='getPoint' :bInputVisable="false"></jyd-map>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-form-item label=" ">
                        <el-button type="primary" :disabled="btnFlag" @click="onSubmit('ruleform')"></el-button>
                        <el-button @click="goback"></el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </div>
    </div>
</template>

<script>
import jdyEditor from '@/components/Quilleditor';
import jydMap from '@/components/BMapComponent.vue';
export default {
    name:"",
    data(){
        return {
            btnFlag:false,
            // 
            ruleform:{
                companyId:JSON.parse(sessionStorage.loginData).uCompanyId, //
                sName:"",
                sSortName:"",
                sCountry :"CN",
                sProvince :"",
                sCity :"",
                sArea :"",
                sAdress :"",  //
                sAdditional :"", //
                sLevel :"", //
                sMapx :"",
                sMapy :"",
                sIntroduce :"", //
                sDetail :"", //
                sAttribute :"", //
                sCustomPlace:"", //
                sStartTime :"", //
                sEndTime:"", //
                sPhone :"" //
            },
            // 
            rules: {
                sName: [{required: true, message: '', trigger: 'blur' }],
                sProvince: [{ required: true, message: '', trigger: 'change' }],
                sCity: [{ required: true, message: '', trigger: 'change' }],
                sArea: [{required: true, message: '', trigger: 'change' }],
                sLevel: [{type:"number",required: true, message: '', trigger: 'change' }],
                sAttribute:[{type:"number", required: true, message: '', trigger: 'change' }],
                sCustomPlace:[{required: true, message: '', trigger: 'blur' }]
            },
            // 
            provinceDate: "",
            cityDate: "",
            areaDate: "",
            provinceList: [],
            cityList: [],
            AreaList: [],
            // 
            bDMapx: null,
            bDMapy: null,
            bcityName: null,
            //
            picShow:false,
            // url
            urlSpotId:this.$route.query.spotId,
            urlAlbumId:this.$route.query.albumId,
            // 
            attributeList:[],
            // 
            levelList:[],
        }
    },
    methods:{
        // type=0;
        toEdit(value){
            if(this.$route.query.type==0){
                this.picShow=true;
                this.$http.get(api_prefix + 'scenic_spot/getById/'+this.$route.query.spotId).then(response => {
                    if(response.data.code == 0){
                        this.ruleform=response.data.body;
                        this.ruleform.sLevel=Number(this.ruleform.sLevel);
                        // 
                        this.bcityName=this.ruleform.sCity;
                        this.ruleform.sMapx=Number(this.ruleform.sMapx);
                        this.ruleform.sMapy=Number(this.ruleform.sMapy);
                        this.bDMapx=this.ruleform.sMapx;
                        this.bDMapy=this.ruleform.sMapy;
                        this.urlAlbumId=response.data.body.albumId;
                    }else{
                        this.$alert("");
                    }
                },response=>{
                    console.log("")
                });
            }
        },
        // ,
        getDiclist(value){
            this.$http.post(api_prefix +"Dictionaries/dictList", {dGroupId: value}).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                };
                if(value==156){
                    this.levelList=response.body.body;
                }else{
                    this.attributeList=response.body.body;
                }
                this.toEdit(value)
            }, response => {
                this.$alert("~", "", { type: 'error' });
            });
        },
        // 
        goSpotInfo(value){
            this.$router.push({ name: "scenicspotInfo",query:{type: value,spotId:this.urlSpotId,albumId:this.urlAlbumId}})
        },
        goPic(value){
            this.$router.push({ name: "scenicspotPic",query:{type: value,spotId:this.urlSpotId,albumId:this.urlAlbumId}})
        },
        goback(){
            this.$router.push({ name: "scenicspot"})
        },
        defaultProvinceData() { //
            let url = api_prefix + '/City/selectCities';
            let city = { level: 1 }
            this.$http.post(url, city).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                }
                this.provinceList = response.body.body;
                if(this.$route.query.type==0){
                    this.provinceChange(this.ruleform.sProvince)
                }
                if (this.ruleform.sProvince) {
                    this.provinceDate = this.ruleform.sProvince;
                }
            }, response => {
                this.$alert("~", "", { type: 'error' });
            });
        },
        provinceChange(value) { //
            let id = 0;
            for (var i in this.provinceList) {
                if (this.provinceList[i].name == value) {
                    id = this.provinceList[i].id;
                }
            }
            if (id == 0 || id==undefined ) {
                return;
            }
            this.ruleform.sProvince = value;
            let url = api_prefix + '/City/selectCities';
            let city = { level: 2, pid: id }
            this.$http.post(url, city).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                }
                this.cityList = response.body.body;
                if(this.$route.query.type==0){
                    this.cityChange(this.ruleform.sCity);
                }
                this.cityDate = '';
                for (var city in this.cityList) {
                    if (this.cityList[city].name == this.ruleform.sCity) {
                        this.cityDate = this.ruleform.sCity;
                        break;
                    }
                }
                this.ruleform.sCity = this.cityDate;
            }, response => {
                this.$alert("~", "", { type: 'error' });
            });
        },
        cityChange(value) { // 
            let id = 0;
            for (var i in this.cityList) {
                if (this.cityList[i].name == value) {
                    id = this.cityList[i].id;
                }
            }
            if (id == 0 || id==undefined ) {
                return;
            }
            this.ruleform.sCity = value;
            let url = api_prefix + '/City/selectCities';
            let city = { level: 3, pid: id };
            this.$http.post(url, city).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                }
                this.AreaList = response.body.body;
                this.areaDate = '';
                for (var p in this.AreaList) {
                    if (this.AreaList[p].name == this.ruleform.sArea) {
                        this.areaDate = this.ruleform.sArea;
                    }
                }
                this.ruleform.sArea = this.areaDate;
            }, response => {
                this.$alert("~", "", { type: 'warning' });
            });
        },
        areaChange(value) {  //
            this.ruleform.sArea = value;
        },
        getPoint(x, y) {
            this.ruleform.sMapx = x;
            this.ruleform.sMapy = y;
        },
        onSubmit(form){
            this.$refs[form].validate((valid) => {
            if(valid){
                let submitform=this.ruleform;
                // 
                let urls="";
                if(this.$route.query.type==0){
                    urls='scenic_spot/update';
                }else{
                    urls='scenic_spot/save';
                };
                // 
                let reg = /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/;
                if(submitform.sPhone!="" && !reg.test(submitform.sPhone)){
                    this.$message.info('');
                    return;
                }
                this.btnFlag = true;
                this.$http.post(api_prefix + urls,submitform).then(response => {
                  this.btnFlag = false;
                  let data = response.body;
                  if(data.code == "0"){
                      if(this.$route.query.type==0){
                          this.$message.success('');
                          this.$router.push({ name: "scenicspot"})
                      }else{
                          this.$confirm('', '', {
                              confirmButtonText: '',
                              cancelButtonText: '',
                              type: 'warning'
                          }).then(() => {
                              this.picShow=true;
                              this.$router.push({ name: "scenicspotPic",query:{type: 0,spotId:data.id[0],albumId:data.body}})
                          }).catch(() => {
                              this.$router.push({ name: "scenicspot"})
                          });
                      }

                  }else{
                      this.$alert(data.message,"",{type: 'error'});
                  }
                },response => {
                    this.btnFlag = false;
                    this.$alert("","",{type: 'error'});
                });
            }else{
                this.$message.error('');
            }
            });
        },
        editorUpdate(value,attr) {
            this.ruleForm[attr] = value;
        }
    },
    components:{
        jdyEditor,
        jydMap
    },
    mounted() {
        this.defaultProvinceData();
        // this.toEdit();
        this.getDiclist(155);
        this.getDiclist(156);
    }
}
</script>

<style>
.el-date-editor.el-input {
    width: 355px;
}
#mapInput input{
    width:200px;
}
</style>



