<template>
    <div class="jdy-content jdy-resources fleft ">
        <div class="jdy-content-inner-trip">
            <div class="tripbox-tab clearfix">
                <a href="javascript:;" title="" class="relative inlineblock fleft active" @click="goHotelInfo($route.query.type)">
                    <span class="absolute inlineblock"></span>
                </a>
                <a title="" href="javascript:;" class="relative inlineblock  fleft" @click="goPic($route.query.type)" v-show="picShow">
                    <span class="absolute inlineblock"></span>
                </a>
            </div>
            <el-form :model="ruleform" :rules="rules" ref="ruleform" class="demo-form" label-width="150px">
                <el-row class="mt30  pr50">
                    <el-col :span="12">
                        <el-form-item label=":" prop="hName">
                            <el-input v-model="ruleform.hName" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":">
                            <el-input v-model="ruleform.hShortName" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="">
                            <el-input  v-model="ruleform.hAdditional" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":" prop="hLevel">
                            <el-select v-model="ruleform.hLevel" clearable placeholder="" class="all">
                                <el-option v-for="item in levelList" :value="item.id" :label="item.dName" :key="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":" prop="">
                            <el-input type="textarea" v-model="ruleform.hIntroduce" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=" " prop="">
                            <el-button type="primary" @click="onSubmit('ruleform')" :disabled="btnFlag"></el-button>
                            <el-button @click="goback"></el-button>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label=":" prop="hProvince">
                            <el-select v-model="ruleform.hProvince" clearable placeholder="" class="all" @change="provinceChange">
                                <el-option v-for="item in provinceList" :value="item.name" :label="item.name" :key="item.id">
                                    <span :kes="item.id">{{item.name}}</span>
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":" prop="hCity">
                            <el-select v-model="ruleform.hCity" clearable placeholder="" class="all" @change="cityChange">
                                <el-option v-for="item in cityList" :value="item.name" :label="item.name" :key="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="/:" prop="hArea">
                            <el-select v-model="ruleform.hArea" clearable placeholder="" class="all" @change="areaChange">
                                <el-option v-for="item in AreaList" :value="item.name" :key="item.name" :label="item.name">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label=":" prop="">
                            <el-input  v-model="ruleform.hAdress" placeholder="">
                            </el-input>
                        </el-form-item>
                        <el-form-item label=":" id="mapPosition">
                            <jyd-map :bDMapx='bDMapx' :bDMapy='bDMapy' :bcityName='bcityName' @mapClick='getPoint' :bInputVisable="false" ></jyd-map>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </div>
    </div>
</template>

<script>
import jydMap from '@/components/BMapComponent.vue';
export default {
    name:"",
    data(){
        return {
            btnFlag:false,
            // 
            ruleform:{
                companyId:JSON.parse(sessionStorage.loginData).uCompanyId, //
                hName:"",
                hShortName:"",
                hCountry :"CN",
                hProvince :"",
                hCity :"",
                hArea :"",
                hAdress :"",  //
                hAdditional :"", //
                hLevel :"", //
                hMapx :null,
                hMapy :null,
                hIntroduce :"",     //
            },
            // 
            rules: {
                hName: [{required: true, message: '', trigger: 'blur' }],
                hProvince: [{ required: true, message: '', trigger: 'change' }],
                hCity: [{ required: true, message: '', trigger: 'change' }],
                hArea: [{required: true, message: '', trigger: 'change' }],
                hLevel: [{ type: 'number',required: true, message: '', trigger: 'change' }],
            },
            // 
            provinceDate: "",
            cityDate: "",
            areaDate: "",
            provinceList: [],
            cityList: [],
            AreaList: [],
            // 
            bDMapx: null,
            bDMapy: null,
            bcityName: null,
            //
            picShow:false,
            // url
            urlHotelId:this.$route.query.hotelId,
            urlAlbumId:this.$route.query.albumId,
            // 
            levelList:[],
        }
    },
    methods:{
        // type=0;
        toEdit(value){
            if(this.$route.query.type==0){
                this.picShow=true;
                this.$http.get(api_prefix + 'hotel/get/'+this.$route.query.hotelId).then(response => {
                    if(response.data.code == 0){
                        this.ruleform=response.data.body;
                        this.ruleform.hLevel=Number(this.ruleform.hLevel);
                        // 
                        this.bcityName=this.ruleform.hCity;
                        this.ruleform.hMapx=Number(this.ruleform.hMapx);
                        this.ruleform.hMapy=Number(this.ruleform.hMapy);
                        this.bDMapx=this.ruleform.hMapx;
                        this.bDMapy=this.ruleform.hMapy;
                        this.urlAlbumId=response.data.body.albumId;
                    }else{
                        this.$alert("");
                    }
                },response=>{
                    console.log("")
                });
            }
        },
        // 
        getDiclist(value){
            this.$http.post(api_prefix +"Dictionaries/dictList", {dGroupId: value}).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                };
                this.levelList=response.body.body;
               this.toEdit(value)
            }, response => {
                this.$alert("~", "", { type: 'error' });
            });
        },
        // 
        goHotelInfo(value){
            this.$router.push({ name: "hotelInfo",query:{type: value,hotelId:this.urlHotelId,albumId:this.urlAlbumId}})
        },
        goPic(value){
            this.$router.push({ name: "hotelPic",query:{type: value,hotelId:this.urlHotelId,albumId:this.urlAlbumId}})
        },
        defaultProvinceData() { //
            let url = api_prefix + '/City/selectCities';
            let city = { level: 1 }
            this.$http.post(url, city).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                }
                this.provinceList = response.body.body;
                if(this.$route.query.type==0){
                    this.provinceChange(this.ruleform.hProvince)
                }
                if (this.ruleform.hProvince) {
                    this.provinceDate = this.ruleform.hProvince;
                }
            }, response => {
                this.$alert("~", "", { type: 'error' });
            });
        },
        provinceChange(value) { //
            let id = 0;
            for (var i in this.provinceList) {
                if (this.provinceList[i].name == value) {
                    id = this.provinceList[i].id;
                }
            }
            if (id == 0 || id==undefined ) {
                return;
            }
            this.ruleform.hProvince = value;
            let url = api_prefix + 'City/selectCities';
            let city = { level: 2, pid: id }
            this.$http.post(url, city).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                }
                this.cityList = response.body.body;
                if(this.$route.query.type==0){
                    this.cityChange(this.ruleform.hCity);
                }
                this.cityDate = '';
                for (var city in this.cityList) {
                    if (this.cityList[city].name == this.ruleform.hCity) {
                        this.cityDate = this.ruleform.hCity;
                        break;
                    }
                }
                this.ruleform.hCity = this.cityDate;
            }, response => {
                this.$alert("~", "", { type: 'error' });
            });
        },
        cityChange(value) { // 
            let id = 0;
            for (var i in this.cityList) {
                if (this.cityList[i].name == value) {
                    id = this.cityList[i].id;
                }
            }
            if (id == 0 || id==undefined ) {
                return;
            }
            this.ruleform.hCity = value;
            let url = api_prefix + '/City/selectCities';
            let city = { level: 3, pid: id };
            this.$http.post(url, city).then(response => {
                if (response.body.code === "-1") {
                    this.$alert(response.body.message, "", { type: 'error' });
                    return
                }
                this.AreaList = response.body.body;
                this.areaDate = '';
                for (var p in this.AreaList) {
                    if (this.AreaList[p].name == this.ruleform.hArea) {
                        this.areaDate = this.ruleform.hArea;
                    }
                }
                this.ruleform.hArea = this.areaDate;
            }, response => {
                this.$alert("~", "", { type: 'warning' });
            });
        },
        areaChange(value) {  //
            this.ruleform.hArea = value;
        },
        getPoint(x, y) {
            this.bDMapx = x;
            this.bDMapy = y;
            this.ruleform.hMapx = x;
            this.ruleform.hMapy = y;
        },
        goback(){
            this.$router.push({ name: "hotel"})
        },
        // 
        onSubmit(form){
            this.$refs[form].validate((valid) => {
            if(valid){
                let submitform=this.ruleform;
                // 
                let urls="";
                if(this.$route.query.type==0){
                    urls='hotel/update';
                }else{
                    urls='hotel/save';
                }
                this.btnFlag = true;
                this.$http.post(api_prefix + urls,submitform).then(response => {
                  this.btnFlag = false;
                let data = response.body;
                if(data.code == "0"){
                    if(this.$route.query.type==0){
                        this.$message.success('');
                        this.$router.push({ name: "hotel"})
                    }else{
                        this.$confirm('', '', {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            this.picShow=true;
                            this.$router.push({ name: "hotelPic",query:{type: 0,hotelId:data.id[0],albumId:data.body}})
                        }).catch(() => {
                            this.$router.push({ name: "hotel"})
                        });
                    }

                }else{
                    this.$alert(data.message,"",{type: 'error'});
                }
                },response => {
                    this.btnFlag = false;
                    this.$alert("","",{type: 'error'});
                });
            }else{
                this.$message.error('');
            }
            });
        }
    },
    components: {
        jydMap
    },
    mounted() {
        this.defaultProvinceData();
        this.getDiclist(239)
       // this.toEdit();
    }
}
</script>

<style>
#mapInput input{
    width:200px;
}
</style>



