window.DateDiff=function(sDate1,sDate2){
  let  aDate,  oDate1,  oDate2,  iDays;
  aDate  =  sDate1.split("-");
  oDate1  =  new  Date(parseInt(aDate[1])  +  '-'  +  parseInt(aDate[2])  +  '-'  +  parseInt(aDate[0])) ;   //12-18-2002
  aDate  =  sDate2.split("-");
  oDate2  =  new  Date(parseInt(aDate[1])  +  '-'  +  parseInt(aDate[2])  +  '-'  +  parseInt(aDate[0]));
  iDays  =  parseInt((oDate1  -  oDate2)  /  1000  /  60  /  60  /24);    //
  return  iDays;
}

